/* App.css */

.direccion {
  margin: 0px;
  font-size: 30px;
}

.texto-generico {
  margin: 10px;
  font-size: 20px
}

/*================================================================*/
/* LOGIN */

.login-container {
  display: flex;
  justify-content: center;
  margin-top: 20px; /* Ajusta este valor según lo necesites */
  background-color: gray;
}

.login-form-container {
  padding: 40px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
}

.login-title {
  margin-bottom: 20px;
  text-align: center;
}

.login-input {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
}

.login-button {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 20px 0;
  border-radius: 5px;
  border: none;
  background-color: #5c6bc0;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/*================================================================*/
/* MAIN SCREEN */

.home-container {
  text-align: center;
  margin-top: 50px
}

.controls-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*-----------------------------------*/
/* Contador de Moviles */

.contadorMoviles-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.arrow-button {
  border: none;
  background-color: transparent;
  padding: 0; /* Elimina el relleno predeterminado */
  margin: 0; /* Elimina el margen predeterminado */
}

.arrow {
  font-size: 60px;
  line-height: 1; /* Reduce el espacio extra alrededor de las flechas */
}

.contadorMoviles-input {
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 30px;
  border: 1px solid black;
  margin: 0; /* Elimina el margen */
  line-height: 50px; /* Alinea el texto verticalmente */
}


/*-----------------------------------*/
/* Tick Box */

.tick-items-container {
  margin: 50px;
}
/* Estilos generales para los items con casillas de tick */
.tick-items-container .tick-item {
  display: flex;
  align-items: center;
  text-align: left;
  margin-bottom: 10px; /* Espacio entre cada item */
}

/* Estilizar los checkboxes */
.tick-items-container input[type="checkbox"] {
  width: 2.5em; /* Tamaño del checkbox */
  height: 2.5em; /* Tamaño del checkbox */
  cursor: pointer;
  -webkit-appearance: none; /* Elimina el estilo por defecto en navegadores WebKit */
  -moz-appearance: none; /* Elimina el estilo por defecto en navegadores Firefox */
  appearance: none; /* Elimina el estilo por defecto en otros navegadores */
  background-color: #f6f6f6; /* Color de fondo */
  border: 2px solid #dcdcdc; /* Borde del checkbox */
  border-radius: 5px; /* Bordes redondeados */
  display: inline-block;
  vertical-align: middle;
}

/* Estilo cuando el checkbox está marcado */
.tick-items-container input[type="checkbox"]:checked {
  background-color: #4caf50; /* Color de fondo cuando está marcado */
  border: 2px solid #4caf50; /* Borde del checkbox cuando está marcado */
}

/* Estilo para la etiqueta asociada al checkbox */
.tick-items-container label {
  margin-left: 0px; /* Espacio entre el checkbox y la etiqueta */
  cursor: pointer;
  font-size: 20px;
}

/*-----------------------------------*/
/* Boton Pedir */

.pedir-button {
  width: 180px;
  height: 180px;
  padding: 10px;
  margin: 5px 5px;
  border-radius: 50%;
  border: none;
  background-color: green;
  color: white;
  font-size: 25px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/*-----------------------------------*/
/* Lista de Moviles */

.moviles-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.movil {
  border: 1px solid black;
  border-radius: 5px;
  text-align: left;
  box-sizing: border-box;
  overflow: auto;
  margin: 10px;
  padding: 10px;
  width: calc(50% - 20px); /* Ajusta el ancho para pantallas pequeñas */
}

@media (min-width: 600px) {
  .movil {
    width: 200px; /* Ancho fijo para pantallas más grandes */
  }
}

/* Estilos para el número de móvil */
.movil-number {
  font-size: 1.2em;
  font-weight: bold;
}

.login-button:hover {
  background-color: #3f51b5;
}

.pedir-button:hover {
  background-color: darkgreen;
}

.cancel-button {
  background-color: red;
}

.cancel-button:hover {
  background-color: darkred;
}
